export default {
	data() {
		return {
			bgColorFind: this.bgColorFind,
			ColorList: this.ColorList,
			TabCur: 0,
			scrollLeft: 0,
			getAvatarType: [],
			getAvatarList: [],
			typeID: '1',
			page: '1'
		}
	},
	created() {
		this._created()
		// 页面加载完
		console.log('页面加载完')
	},
	methods:{
		_created(){
			//	获取头像类型
			this.getAvatarTypeData()
			this.getAvatarListData()
			console.log('avatar------')
		},
		getAvatarTypeData(){
			this.request.globalRequest(this.route.apiAvatarType).then((res)=>{
				this.getAvatarType = res.data
				console.log(this.getAvatarType)
			})
		},
		getAvatarListData(){
			const typeID = this.typeID
			const param = {
				type_id: typeID,
				page: this.page
			}
			
			this.request.globalRequest(this.route.apiAvatarList, param).then((res)=>{
				this.getAvatarList = res.data.data
				console.log(this.getAvatarList)
			})
		},
		tabSelect(index,typeID) {
			console.log('切换栏目');
			
			console.log(index);
			console.log(typeID);
			this.TabCur = index
			this.typeID = typeID
			this.scrollLeft = (index - 1) * 60
			
			this.getAvatarListData()
			// this.TabCur = e.currentTarget.dataset.id;
			// this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60
		},
		onAvatarShow (){
			console.log('xx')
			console.log('open')
			// 打开:荔枝墙小程序
			uni.navigateToMiniProgram({
			  appId: 'wx9763afd7127696fc',
			  path: '',
			  extraData: {
			    'data1': 'test'
			  },
			  success(res) {
			    // 打开成功
				console.log('open-success')
			  }
			})
			
		}
		// 
	}
}
